@import url("https://fonts.googleapis.com/css?family=Baloo+Da+2&display=swap");

body {
    margin: 0px;
    overflow: hidden;
    font-family: "Baloo Da 2", cursive;
}

.world-main-container {
    height: 100vh;
    width: 100vw;
}

.world-main-container {
    div {
        height: 100vh;
    }
}

.world-main-container-right div:nth-child(1) {
    min-height: 50vh;
    max-height: 50vh;
    overflow: hidden;
}

.world-main-container-right div:nth-child(2) {
    min-height: 50vh;
    max-height: 50vh;
    display: flex;
    justify-content: center;
    padding-top: 10px;
}

#map-container {
    height: 603px;
    width: 468px;
}

.world-main-container-flags {
    height: 100%;
    .world-flag {
        width: 90%;
        cursor: pointer;
        border: solid 1px darkgrey
    }
}

#country-population-body {
    height: 500px;
    width: 550px;
}
